/* global cornerstone */

import { utils } from '@ohif/core'
import React, { useState, useEffect, createRef } from 'react'
import styled, { keyframes } from 'styled-components'
import PropTypes from 'prop-types'
import ViewportErrorIndicator from './ViewportErrorIndicator'
import ViewportLoadingIndicator from './ViewportLoadingIndicator'

// TODO: How should we have this component depend on Cornerstone?
// - Passed in as a prop?
// - Set as external dependency?
// - Pass in the entire load and render function as a prop?
//import cornerstone from 'cornerstone-core';
function ImageThumbnail(props) {
  const {
    width,
    height,
    imageSrc,
    imageId,
    stackPercentComplete,
    error: propsError,
  } = props

  const [isLoading, setLoading] = useState(false)
  const [error, setError] = useState(false)
  const [image, setImage] = useState({})
  const canvasRef = createRef()

  let loadingOrError
  let cancelablePromise

  if (propsError || error) {
    loadingOrError = <ViewportErrorIndicator />
  } else if (isLoading) {
    loadingOrError = <ViewportLoadingIndicator />
  }

  const showStackLoadingProgressBar = stackPercentComplete !== undefined

  const shouldRenderToCanvas = () => {
    return imageId && !imageSrc
  }

  // eslint-disable-next-line
  const fetchImagePromise = () => {
    if (!cancelablePromise) {
      return
    }

    setLoading(true)
    cancelablePromise
      .then(response => {
        setImage(response)
      })
      .catch(error => {
        if (error.isCanceled) return
        setLoading(false)
        setError(true)
        throw new Error(error)
      })
  }

  // eslint-disable-next-line
  const setImagePromise = () => {
    if (shouldRenderToCanvas()) {
      cancelablePromise = utils.makeCancelable(
        cornerstone.loadAndCacheImage(imageId)
      )
    }
  }

  // eslint-disable-next-line
  const purgeCancelablePromise = () => {
    if (cancelablePromise) {
      cancelablePromise.cancel()
    }
  }

  useEffect(() => {
    return () => {
      purgeCancelablePromise()
    }
  }, [purgeCancelablePromise])

  useEffect(() => {
    if (image.imageId) {
      cornerstone.renderToCanvas(canvasRef.current, image)
      setLoading(false)
    }
  }, [canvasRef, image, image.imageId])

  useEffect(() => {
    if (!image.imageId || image.imageId !== imageId) {
      purgeCancelablePromise()
      setImagePromise()
      fetchImagePromise()
    }
  }, [
    fetchImagePromise,
    image.imageId,
    imageId,
    purgeCancelablePromise,
    setImagePromise,
  ])

  return (
    <ImageThumbnailWrapper className="ImageThumbnail">
      <div className="image-thumbnail-canvas">
        {shouldRenderToCanvas() ? (
          <canvas ref={canvasRef} width={width} height={height} />
        ) : (
          <img
            className="static-image"
            src={imageSrc}
            //width={this.props.width}
            height={height}
            alt={''}
          />
        )}
      </div>
      {loadingOrError}
      {showStackLoadingProgressBar && (
        <div className="image-thumbnail-progress-bar">
          <div
            className="image-thumbnail-progress-bar-inner"
            style={{ width: `${stackPercentComplete}%` }}
          />
        </div>
      )}
      {isLoading && <div className="image-thumbnail-loading-indicator"></div>}
    </ImageThumbnailWrapper>
  )
}

const imageThumbnailLoadingIndicator = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`

const ImageThumbnailWrapper = styled.div`
  .ThumbnailEntry.active .ImageThumbnail {
    box-shadow: none;
    transition: all 0.3s ease;
  }

  &.ImageThumbnail {
    border-radius: 12px;
    height: 135px;
    margin: 0 auto;
    padding 5px;
    position: relative;
    transition: all 0.3s ease;
    width: 217px;
    background-clip: padding-box;

    .image-thumbnail-loading-indicator {
      position: absolute;
      left: 50%;
      top: 50%;
      margin: -22px 0 0 -22px;
      display: block;
      width: 44px;
      height: 44px;

      &:after {
        content: " ";
        display: block;
        width: 32px;
        height: 32px;
        margin: 1px;
        border-radius: 50%;
        border: 5px solid ${p => p.theme.primary};
        border-color: ${p => p.theme.primary} transparent;
        animation: ${imageThumbnailLoadingIndicator} 1.2s linear infinite;
      }
    }
  }

  .image-thumbnail-canvas {
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;

    img {
      -webkit-user-drag: none;
      pointer-events: none;
    }
  }

  .thumbnailLoadingIndicator {
    display: none;
    pointer-events: none;
    height: 20px;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    position: absolute;

    &.d-block {
      display: block;
    }

    p {
      text-align: center;
      font-size: 10pt;
    }
  }

  .image-thumbnail-progress-bar {
    position: relative;
    width: 100%;
    height: 3px;
    top: -5px;

    .image-thumbnail-progress-bar-inner {
      height: 100%;
      width: 0;
      border-radius: 5px;
    }
  }
`

ImageThumbnail.propTypes = {
  imageSrc: PropTypes.string,
  imageId: PropTypes.string,
  error: PropTypes.bool,
  width: PropTypes.number,
  height: PropTypes.number,
  stackPercentComplete: PropTypes.number.isRequired,
}

ImageThumbnail.defaultProps = {
  error: false,
  stackPercentComplete: 0,
  width: 217,
  height: 123,
}

export default ImageThumbnail
