import React from 'react'
import { Provider } from 'react-redux'
import { utils } from '@ohif/core'
import setConfiguration from './utils/setConfiguration'
import initWebWorkers from './utils/initWebWorker'
import ModalProvider from './contexts/ModalContext'
import DialogProvider from './contexts/DialogContext'
import Modal from './components/Modal'
import App from './App'
import store from './configureStore'
import defaultConfig from './config'
import {
  commandsManager,
  servicesManager,
  hotkeysManager,
  extensionManager,
  AppProvider,
} from './AppContext'

// TODO[react] Use a provider when the whole tree is React
window.store = store

window.ohif = window.ohif || {}
window.ohif.app = {
  commandsManager,
  hotkeysManager,
  servicesManager,
  extensionManager,
}

let config = {}

if (window) {
  config = window.config || defaultConfig
}

const appDefaultConfig = {
  showStudyList: true,
  cornerstoneExtensionConfig: {},
  extensions: [],
}

const appConfig = {
  ...appDefaultConfig,
  ...(typeof config === 'function' ? config({ servicesManager }) : config),
}

if (appConfig.servers) {
  utils.addServers(appConfig.servers, store)
}

setConfiguration(appConfig)
initWebWorkers()

export function ViewerWithStudies(props) {
  const { UIDialogService, UIModalService } = servicesManager.services

  return (
    <Provider store={store}>
      <AppProvider config={appConfig}>
        <DialogProvider service={UIDialogService}>
          <ModalProvider modal={Modal} service={UIModalService}>
            <App {...props} />
          </ModalProvider>
        </DialogProvider>
      </AppProvider>
    </Provider>
  )
}

export { default as Thumbnail } from './components/Thumbnail'

export { default as ToolbarRow } from './components/ToolbarRow'

export { default as ViewerMain } from './components/ViewerMain'

export { default as StudyBrowser } from './components/StudyBrowser'
