import React from 'react'
import { useSelector } from 'react-redux'
import {
  CommandsManager,
  ServicesManager,
  HotkeysManager,
  MeasurementService,
  UIModalService,
  UIDialogService,
  ExtensionManager,
} from '@ohif/core'
import OHIFCornerstoneExtension from '@ohif/extension-cornerstone'
import GenericViewerCommands from './extensions/commandModule'
import store from './configureStore'
import { getActiveContexts } from './reducers/layout'
import defaultConfig from './config'

export let AppContext = React.createContext({})

export const CONTEXTS = {
  CORNERSTONE: 'ACTIVE_VIEWPORT::CORNERSTONE',
  VTK: 'ACTIVE_VIEWPORT::VTK',
}

export const useAppContext = () => React.useContext(AppContext)

export const AppProvider = ({ children, config }) => {
  const activeContexts = useSelector(state => getActiveContexts(state))

  return (
    <AppContext.Provider value={{ appConfig: config, activeContexts }}>
      {children}
    </AppContext.Provider>
  )
}

export const withAppContext = Component => {
  return function WrappedComponent(props) {
    const { appConfig, activeContexts } = useAppContext()
    return (
      <Component
        {...props}
        appConfig={appConfig}
        activeContexts={activeContexts}
      />
    )
  }
}

export const servicesManager = new ServicesManager(
  // UINotificationService,
  UIModalService,
  UIDialogService,
  MeasurementService
)

servicesManager.registerServices([MeasurementService])

let config = {}

if (window) {
  config = window.config || defaultConfig
}

const appDefaultConfig = {
  showStudyList: true,
  cornerstoneExtensionConfig: {},
  extensions: [],
}

const appConfig = {
  ...appDefaultConfig,
  ...(typeof config === 'function' ? config({ servicesManager }) : config),
}

const {
  hotkeys: appConfigHotkeys,
  cornerstoneExtensionConfig,
  // extensions,
} = appConfig

/** ~~~~~~~~~~~~~ Application Setup */
const commandsManagerConfig = {
  getAppState: () => store.getState(),
  getActiveContexts: () => getActiveContexts(store.getState()),
}

/** Managers */
export const commandsManager = new CommandsManager(commandsManagerConfig)

export const hotkeysManager = new HotkeysManager(
  commandsManager,
  servicesManager
)

// TODO: Use something more resilient
// TODO: Mozilla has a special library for this
const userPreferredHotkeys = JSON.parse(
  localStorage.getItem('hotkey-definitions') || '{}'
)

// TODO: hotkeysManager.isValidDefinitionObject(/* */)
const hasUserPreferences =
  userPreferredHotkeys && Object.keys(userPreferredHotkeys).length > 0
if (hasUserPreferences) {
  hotkeysManager.setHotkeys(userPreferredHotkeys)
} else {
  hotkeysManager.setHotkeys(appConfigHotkeys)
}

hotkeysManager.setDefaultHotKeys(appConfigHotkeys)

export const extensionManager = new ExtensionManager({
  commandsManager,
  servicesManager,
  appConfig,
  api: {
    contexts: CONTEXTS,
    hooks: {
      useAppContext,
    },
  },
})

const requiredExtensions = [
  GenericViewerCommands,
  [OHIFCornerstoneExtension, cornerstoneExtensionConfig],
]

if (appConfig.disableMeasurementPanel !== true) {
  /* WARNING: MUST BE REGISTERED _AFTER_ OHIFCornerstoneExtension */
  // requiredExtensions.push(MeasurementsPanel)
}

const mergedExtensions = requiredExtensions.concat([
  // TODO: add extension-html, extension-pdf...etc
])
extensionManager.registerExtensions(mergedExtensions)
