import React from 'react'
import PropTypes from 'prop-types'
import { useDrag } from 'react-dnd'
import classNames from 'classnames'
import styled from 'styled-components'
import ImageThumbnail from './ImageThumbnail'

function ThumbnailFooter({
  SeriesDescription,
  SeriesNumber,
  InstanceNumber,
  numImageFrames,
}) {
  const infoOnly = !SeriesDescription

  const getInfo = (value, icon, className = '') => {
    return (
      <div
        className={classNames('item item-series flex items-center', className)}>
        <div className="icon">{icon}</div>
        <div className="value">{value}</div>
      </div>
    )
  }
  const getSeriesInformation = (
    SeriesNumber,
    InstanceNumber,
    numImageFrames
  ) => {
    if (!SeriesNumber && !InstanceNumber && !numImageFrames) {
      return
    }

    return (
      <div className="series-information">
        {getInfo(SeriesNumber, 'S:')}
        {getInfo(InstanceNumber, 'I:')}
        {getInfo(numImageFrames, '', 'image-frames')}
      </div>
    )
  }

  return (
    <div className={classNames('series-details', { 'info-only': infoOnly })}>
      <div className="series-description">{SeriesDescription}</div>
      {getSeriesInformation(SeriesNumber, InstanceNumber, numImageFrames)}
    </div>
  )
}

function Thumbnail(props) {
  const {
    active,
    altImageText,
    error,
    displaySetInstanceUID,
    imageId,
    imageSrc,
    // InstanceNumber,
    // numImageFrames,
    // SeriesDescription,
    // SeriesNumber,
    stackPercentComplete,
    StudyInstanceUID,
    onClick,
    onDoubleClick,
    onMouseDown,
    supportsDrag,
  } = props

  const [, drag] = useDrag({
    // `droppedItem` in `dropTarget`
    // The only data it will have access to
    item: {
      StudyInstanceUID,
      displaySetInstanceUID,
      type: 'thumbnail', // Has to match `dropTarget`'s type
    },
    canDrag: function (monitor) {
      return supportsDrag
    },
  })

  const hasImage = imageSrc || imageId
  const hasAltText = altImageText !== undefined

  return (
    <ThumbnailWrapper
      ref={drag}
      className={classNames('thumbnail', { active: active })}
      onClick={onClick}
      onDoubleClick={onDoubleClick}
      onMouseDown={onMouseDown}>
      {/* SHOW IMAGE */}
      {hasImage && (
        <ImageThumbnail
          imageSrc={imageSrc}
          imageId={imageId}
          error={error}
          stackPercentComplete={stackPercentComplete}
        />
      )}
      {/* SHOW TEXT ALTERNATIVE */}
      {!hasImage && hasAltText && (
        <div className={'alt-image-text p-x-1'}>
          <h1>{altImageText}</h1>
        </div>
      )}
      {ThumbnailFooter(props)}
    </ThumbnailWrapper>
  )
}

const ThumbnailWrapper = styled.div`
  &.thumbnail {
    cursor: pointer;
    display: table;
    margin: 0 auto;
    min-width: 90%;
    padding-bottom: 0.75rem;
    /* border: 1px solid transparent;
    transition: all 0.3s;

    &:hover {
      border: 1px solid ${p => p.theme.primary};
    } */

    .alt-image-text {
      align-items: center;
      justify-content: center;
      border: 5px solid transparent;
      border-radius: 12px;
      height: 135px;
      margin: 0 auto;
      padding: 5px;
      position: relative;
      transition: all 0.3s ease;
      width: 217px;
      display: flex;

      h1 {
        text-align: center;
      }
    }

    .series-details {
      display: flex;
      flex-direction: column;
      font-size: 14px
      line-height: 1.3em;
      margin-top: 5px;
      margin-right: 0.5rem;
      max-width: 100%;
      position: relative;
      word-wrap: break-word;

      .series-description {
        flex-grow: 1;
      }

      &.info-only .series-description {
        display: none;
      }
    }

    .series-information {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      .item-frames .icon {
        height: 18px;
      }

      .item-series.image-frames .icon {
        background-color: #678696;
        margin-top: 6px;
        position: relative;

        &:after {
          content: ''
          background-color: #20a5d6;
          box-shadow: 1px 1px rgba(0, 0, 0, .115);
          left: -4px;
          position: absolute;
          top: -4px;
        }

        &:after {
          content: '';
          border: 0px solid #678696;
          display: inline-block;
          height: 11px;
          width: 11px;
        }
      }

      .value {
        display: inline-block;
        font-size: 12px;
        margin-left: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: calc(100% - 15px);
        vertical-align: middle;
      }

      .icon  {
        display: inline-block;
        font-size: 10px;
        font-weight: 900;
        text-align: right;
        width: 11px;
      }
    }
  }
`

const noop = () => {}

Thumbnail.propTypes = {
  supportsDrag: PropTypes.bool,
  id: PropTypes.string.isRequired,
  displaySetInstanceUID: PropTypes.string.isRequired,
  StudyInstanceUID: PropTypes.string.isRequired,
  imageSrc: PropTypes.string,
  imageId: PropTypes.string,
  error: PropTypes.bool,
  active: PropTypes.bool,
  stackPercentComplete: PropTypes.number,
  /**
  altImageText will be used when no imageId or imageSrc is provided.
It will be displayed inside the <div>. This is useful when it is difficult
  to make a preview for a type of DICOM series (e.g. DICOM-SR)
  */
  altImageText: PropTypes.string,
  SeriesDescription: PropTypes.string,
  SeriesNumber: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  InstanceNumber: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  numImageFrames: PropTypes.number,
  onDoubleClick: PropTypes.func,
  onClick: PropTypes.func,
  onMouseDown: PropTypes.func,
}

Thumbnail.defaultProps = {
  supportsDrag: false,
  active: false,
  error: false,
  stackPercentComplete: 0,
  onDoubleClick: noop,
  onClick: noop,
  onMouseDown: noop,
}

export default Thumbnail
