import React from 'react'
import { Card, Row, Col } from 'antd'
import cx from 'classnames'
import { RouteComponentProps } from 'react-router-dom'
import { Page } from '@/components/layout'
import Input from '@/components/input'
import Collapse from '@/components/collapse'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import Button from '@/components/button'
import useVisible from '@/hooks/useVisible'
import {
  ViewerWithStudies,
  ViewerMain,
  ToolbarRow,
  StudyBrowser,
} from '@/dicom-viewer'

interface Props extends RouteComponentProps<{ studyInstanceUIDs: string }> {}

export default function RadiolImageViewer(props: Props) {
  useTitle('放射影像')

  useBreadcrumb([{ name: '輸入報告' }, { name: '放射影像' }])

  const [annotationListVisible, handleAnnotationListVisible] = useVisible()

  useHeaderContent(
    <div className="inline-flex">
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleAnnotationListVisible}>
        Annotation List
      </Button>
    </div>
  )

  return (
    <Page className="pr-8">
      <ViewerWithStudies>
        {({ state, props, onChange }: any) => (
          <div className="flex">
            <Card
              className="rounded-lg shadow-2xl h-full mr-4"
              style={{ width: 250 }}
              bodyStyle={{ padding: 12, paddingTop: 24 }}>
              <Row className="mb-2 text-sm text-primary">
                <Col span={24} className="mb-3">
                  <div className="inline-block" css={{ width: 65 }}>
                    審核醫師
                  </div>
                  <Input className="w-2/3" bordered={false} />
                </Col>
                <Col span={24} className="my-3">
                  <div className="inline-block" css={{ width: 65 }}>
                    申請序號
                  </div>
                  <Input className="w-2/3" bordered={false} />
                </Col>
                <Col span={24} className="my-3">
                  <div className="inline-block" css={{ width: 65 }}>
                    報告格式
                  </div>
                  <Input className="w-2/3" bordered={false} />
                </Col>
                <Col span={24} className="mt-3">
                  <StudyBrowser
                    studies={state.thumbnails}
                    studyMetadata={props.studies}
                  />
                </Col>
              </Row>
            </Card>
            <Row className="flex-1" gutter={16}>
              <Col span={annotationListVisible ? 20 : 24}>
                <div
                  className="p-8 bg-white rounded-lg shadow-2xl flex flex-col"
                  // style={{ height: '100vh' }}
                >
                  <ToolbarRow
                    isLeftSidePanelOpen={state.isLeftSidePanelOpen}
                    isRightSidePanelOpen={state.isRightSidePanelOpen}
                    selectedLeftSidePanel={
                      state.isLeftSidePanelOpen
                        ? state.selectedLeftSidePanel
                        : ''
                    }
                    selectedRightSidePanel={
                      state.isRightSidePanelOpen
                        ? state.selectedRightSidePanel
                        : ''
                    }
                    handleSidePanelChange={(side: any, selectedPanel: any) => {
                      const sideClicked =
                        side && side[0].toUpperCase() + side.slice(1)
                      const openKey = `is${sideClicked}SidePanelOpen`
                      const selectedKey = `selected${sideClicked}SidePanel`
                      const updatedState = Object.assign({}, state)

                      const isOpen = updatedState[openKey]
                      const prevSelectedPanel = updatedState[selectedKey]
                      // RoundedButtonGroup returns `null` if selected button is clicked
                      const isSameSelectedPanel =
                        prevSelectedPanel === selectedPanel ||
                        selectedPanel === null

                      updatedState[selectedKey] =
                        selectedPanel || prevSelectedPanel

                      const isClosedOrShouldClose =
                        !isOpen || isSameSelectedPanel
                      if (isClosedOrShouldClose) {
                        updatedState[openKey] = !updatedState[openKey]
                      }

                      onChange(updatedState)
                    }}
                    studies={props.studies}
                  />
                  <ViewerMain
                    studies={props.studies}
                    isStudyLoaded={props.isStudyLoaded}
                  />
                </div>
              </Col>
              {annotationListVisible && (
                <Col className={cx('flex-1', {})}>
                  <Collapse
                    activeKey={[1, 2, 3, 4]}
                    expandIcon={() => null}
                    className="shadow-2xl">
                    <Collapse.Panel
                      header={
                        <div className="text-center">Annotation List</div>
                      }
                      key={1}>
                      <div className="border-b border-gray-600 p-3">
                        申請序號
                      </div>
                      <div className="border-b border-gray-600 p-3">病理號</div>
                      <div className="border-b border-gray-600 p-3">
                        病患姓名
                      </div>
                      <div className="border-b border-gray-600 p-3">性別</div>
                      <div className="border-b border-gray-600 p-3">
                        申請科別
                      </div>
                      <div className="border-b border-gray-600 p-3">病例號</div>
                      <div className="border-b border-gray-600 p-3">
                        病房床號
                      </div>
                      <div className="border-b border-gray-600 p-3">
                        出生日期
                      </div>
                      <div className="border-b border-gray-600 p-3">
                        醫屬名稱
                      </div>
                      <div className="border-b border-gray-600 p-3">
                        現在輸入
                      </div>
                      <div className="border-b border-gray-600 p-3">審核者</div>
                    </Collapse.Panel>
                  </Collapse>
                </Col>
              )}
            </Row>
          </div>
        )}
      </ViewerWithStudies>
    </Page>
  )
}
