import React from 'react'
import PropTypes from 'prop-types'
import OHIF from '@ohif/core'
import styled from 'styled-components'
import { connect } from 'react-redux'
import { clone } from 'ramda'
import Thumbnail from './Thumbnail'

// const { studyMetadataManager } = OHIF.utils

const { setActiveViewportSpecificData } = OHIF.redux.actions

/**
 * Finds displaySet by UID across all displaySets inside studyMetadata
 * @param {Array} studyMetadata
 * @param {string} displaySetInstanceUID
 */
function findDisplaySetByUID(studyMetadata, displaySetInstanceUID) {
  if (!Array.isArray(studyMetadata)) return null

  const allDisplaySets = studyMetadata.reduce((all, current) => {
    let currentDisplaySet = []
    if (current && Array.isArray(current.displaySets)) {
      currentDisplaySet = current.displaySets
    }
    return all.concat(currentDisplaySet)
  }, [])

  const bySetInstanceUID = ds =>
    ds.displaySetInstanceUID === displaySetInstanceUID

  const displaySet = allDisplaySets.find(bySetInstanceUID)
  return displaySet || null
}

function StudyBrowser(props) {
  const {
    studies,
    onThumbnailClick,
    onThumbnailDoubleClick,
    supportsDrag,
  } = props

  return (
    <StyledBrowserWrapper className="study-browser">
      <div className="scrollable-study-thumbnails">
        {studies
          .map((study, studyIndex) => {
            const { StudyInstanceUID } = study
            return study.thumbnails.map((thumb, thumbIndex) => {
              // TODO: Thumb has more props than we care about?
              const {
                altImageText,
                displaySetInstanceUID,
                imageId,
                InstanceNumber,
                numImageFrames,
                SeriesDescription,
                SeriesNumber,
                stackPercentComplete,
              } = thumb

              return (
                <div
                  key={thumb.displaySetInstanceUID}
                  className="thumbnail-container"
                  data-cy="thumbnail-list">
                  <Thumbnail
                    supportsDrag={supportsDrag}
                    key={`${studyIndex}_${thumbIndex}`}
                    id={`${studyIndex}_${thumbIndex}`} // Unused?
                    // Study
                    StudyInstanceUID={StudyInstanceUID} // used by drop
                    // Thumb
                    altImageText={altImageText}
                    imageId={imageId}
                    InstanceNumber={InstanceNumber}
                    displaySetInstanceUID={displaySetInstanceUID} // used by drop
                    numImageFrames={numImageFrames}
                    SeriesDescription={SeriesDescription}
                    SeriesNumber={SeriesNumber}
                    stackPercentComplete={stackPercentComplete}
                    // Events
                    onClick={onThumbnailClick.bind(
                      undefined,
                      displaySetInstanceUID
                    )}
                    onDoubleClick={onThumbnailDoubleClick}
                  />
                </div>
              )
            })
          })
          .flat()}
      </div>
    </StyledBrowserWrapper>
  )
}

const StyledBrowserWrapper = styled.div`
  &.study-browser {
    float: left;
    height: 100%;
    width: 100%;
    overflow: hidden;
    padding-top: 10px;

    .scrollable-study-thumbnails {
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;

      display: flex;
      flex-direction: column;

      &::-webkit-scrollbar {
        display: none;
      }

      .thumbnail-container {
        margin: 0 auto;
        padding-bottom: 0.5rem;

        .noselect {
          -moz-user-select: none; /* Firefox */
          -ms-user-select: none; /* Internet Explorer */
          -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
          -webkit-user-select: none; /* Chrome, Safari, and Opera */
          -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
        }
      }
    }
  }

  .draggable {
    cursor: copy;
  }
`

const noop = () => {}

StudyBrowser.propTypes = {
  studies: PropTypes.arrayOf(
    PropTypes.shape({
      StudyInstanceUID: PropTypes.string.isRequired,
      thumbnails: PropTypes.arrayOf(
        PropTypes.shape({
          altImageText: PropTypes.string,
          displaySetInstanceUID: PropTypes.string.isRequired,
          imageId: PropTypes.string,
          InstanceNumber: PropTypes.number,
          numImageFrames: PropTypes.number,
          SeriesDescription: PropTypes.string,
          SeriesNumber: PropTypes.number,
          stackPercentComplete: PropTypes.number,
        })
      ),
    })
  ).isRequired,
  supportsDrag: PropTypes.bool,
  onThumbnailClick: PropTypes.func,
  onThumbnailDoubleClick: PropTypes.func,
}

StudyBrowser.defaultProps = {
  studies: [],
  supportsDrag: true,
  onThumbnailClick: noop,
  onThumbnailDoubleClick: noop,
}

// TODO
// - Determine in which display set is active from Redux (activeViewportIndex and layout viewportData)
// - Pass in errors and stack loading progress from Redux
const mapStateToProps = (state, ownProps) => {
  // If we know that the stack loading progress details have changed,
  // we can try to update the component state so that the thumbnail
  // progress bar is updated
  const stackLoadingProgressMap = state.loading.progress
  const studiesWithLoadingData = clone(ownProps.studies)

  studiesWithLoadingData.forEach(study => {
    study.thumbnails.forEach(data => {
      const { displaySetInstanceUID } = data
      const stackId = `StackProgress:${displaySetInstanceUID}`
      const stackProgressData = stackLoadingProgressMap[stackId]

      let stackPercentComplete = 0
      if (stackProgressData) {
        stackPercentComplete = stackProgressData.percentComplete
      }

      data.stackPercentComplete = stackPercentComplete
    })
  })

  return {
    studies: studiesWithLoadingData,
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    onThumbnailClick: displaySetInstanceUID => {
      let displaySet = findDisplaySetByUID(
        ownProps.studyMetadata,
        displaySetInstanceUID
      )

      if (displaySet.isDerived) {
        const { Modality } = displaySet

        displaySet = displaySet.getSourceDisplaySet(ownProps.studyMetadata)

        if (!displaySet) {
          throw new Error(
            `Referenced series for ${Modality} dataset not present.`
          )
        }

        if (!displaySet) {
          throw new Error('Source data not present')
        }
      }

      dispatch(setActiveViewportSpecificData(displaySet))
    },
  }
}

const ConnectedStudyBrowser = connect(
  mapStateToProps,
  mapDispatchToProps
)(StudyBrowser)

export default ConnectedStudyBrowser
