import classNames from 'classnames'
import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

const propTypes = {
  /** Sets the direction the Tooltip is positioned towards. */
  placement: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),

  /** The "top" position value for the Tooltip. */
  positionTop: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  /** The "left" position value for the Tooltip. */
  positionLeft: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),

  /** The "top" position value for the Tooltip arrow. */
  arrowOffsetTop: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  /** The "left" position value for the Tooltip arrow. */
  arrowOffsetLeft: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
}

const defaultProps = {
  placement: 'right',
}

class Tooltip extends React.Component {
  render() {
    const {
      placement,
      positionTop,
      positionLeft,
      arrowOffsetTop,
      arrowOffsetLeft,
      className,
      style,
      children,
    } = this.props

    const outerStyle = {
      top: positionTop,
      left: positionLeft,
      ...style,
    }

    const arrowStyle = {
      top: arrowOffsetTop,
      left: arrowOffsetLeft,
    }

    return (
      <TooltipWrapper
        role="tooltip"
        className={classNames(className, 'tooltip', placement)}
        style={outerStyle}>
        <div className="tooltip-arrow" style={arrowStyle} />
        <div className="tooltip-inner">{children}</div>
      </TooltipWrapper>
    )
  }
}

const TooltipWrapper = styled.div`
  .tooltip {
    position: absolute;
    z-index: 1070;
    display: block;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: left;
    text-align: start;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    letter-spacing: normal;
    word-break: normal;
    word-spacing: normal;
    word-wrap: normal;
    white-space: normal;
    opacity: 0;
    line-break: auto;

    &.in {
      opacity: 0.9;
    }

    &.top {
      margin-top: -3px;
      padding: 5px 0;
    }

    &.right {
      margin-left: 3px;
      padding: 0 5px;
    }

    &.bottom {
      margin-top: 3px;
      padding: 5px 0;
    }
    &.left {
      margin-left: -3px;
      padding: 0 5px;
    }
    &.top .tooltip-arrow {
      bottom: 0;
      left: 50%;
      margin-left: -5px;
      border-width: 5px 5px 0;
      border-top-color: #000;
    }

    &.right .tooltip-arrow {
      top: 50%;
      left: 0;
      margin-top: -5px;
      border-width: 5px 5px 5px 0;
      border-right-color: #000;
    }
    &.left .tooltip-arrow {
      top: 50%;
      right: 0;
      margin-top: -5px;
      border-width: 5px 0 5px 5px;
      border-left-color: #000;
    }
    &.bottom .tooltip-arrow {
      top: 0;
      left: 50%;
      margin-left: -5px;
      border-width: 0 5px 5px;
      border-bottom-color: #000;
    }
  }

  .tooltip-inner {
    max-width: 200px;
    padding: 3px 8px;
    color: #fff;
    text-align: center;
    background-color: #000;
    border-radius: 4px;
  }

  .tooltip-arrow {
    position: absolute;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
  }

  .tooltip.in {
    opacity: 0.9;
  }
`

Tooltip.propTypes = propTypes
Tooltip.defaultProps = defaultProps

export default Tooltip
